
import qs from "qs";
import Group from 'vux/src/components/group/index.vue'
import PopupRadio from 'vux/src/components/popup-radio/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Alert from 'vux/src/components/alert/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'

export default {
directives: {
TransferDom
},
components: {
Group,
PopupRadio,
Alert,
Popup,
Confirm
},
data() {
return {
playerName: "",
certificateType: "",
certificateNo: "",
phone: "",
chessLevel: "",
chessLevelName: "",
titleTips: "",
unitCode: "",
showUnitCode: false,
showAlert: false,
codeList: [],
currentIndex: "",
showCofirm: false,
id:''
};
},
methods: {
showUnitCodePage() {
this.showUnitCode = true;
let params = {
unitType: 1
};
this.$axios.get("/api/units/list", { params }).then(res => {
console.log(res);
if (res.data.code == 0) {
this.codeList = res.data.data;
}
});
},
canclePlayerBtn() {
this.showUnitCode = false;
},
selctedCode(item, index) {
this.unitCode = item.unitCode;
this.currentIndex = index;
this.disabled = false;
setTimeout(() => {
this.currentIndex = "";
this.showUnitCode = false;
}, 500);
},
checkPlayerPhone() {
let reg = /^1[3|4|5|8][0-9]\d{4,8}$/;
if (!reg.test(this.phone)) {
this.showAlert = true;
this.titleTips = "请输入正确的电话号码";
return false;
} else {
return true;
}
},
saveDate() {
let params = {
id:this.id,
playerName:this.playerName,
certificateType:this.certificateType,
certificateNo:this.certificateNo,
phone:this.phone,
unitCode:this.unitCode
};
this.$axios.post("/api/player/player_edit", qs.stringify(params)).then(res => {
if(res.data.code == 0){
this.$router.push({ name: "commonInformation" });
}
});
},
showConfiremPart() {
this.showCofirm = true;
},
onCancel() {
this.showCofirm = false;
},
onConfirm() {
let params ={
id:this.id
}
this.$axios.get('/api/player/player_del',{params}).then( res =>{
if(res.data.code == 0){
this.$router.push({ name: "commonInformation" });
}
})
}
},
created() {
let obj = JSON.parse(sessionStorage.getItem("editCurrentPlayerInfo"));
let id = obj.id;
let params = {
id: id
};
this.$axios.get("/api//player/get_by_id", { params }).then(res => {
if (res.data.code == 0) {
let rst = res.data.data[0];
console.log(res, "rst");
this.playerName = rst.playerName;
this.certificateType = rst.certificateType;
this.certificateNo = rst.certificateNo;
this.unitCode = rst.unitCode;
this.phone = rst.phone;
this.id = rst.id
this.chessLevelName = rst.chessLevelName;


}
});
}
};



